import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { ToastController } from 'ionic-angular';
import { getRepository, Repository } from 'typeorm';

import { City } from '../../entities/City';
import { Client } from '../../entities/Client';
import { PaymentMethod } from '../../entities/PaymentMethod';
import { Photo } from '../../entities/Photo';
import { User } from '../../entities/User';
import { Category } from '../../entities/Category';
import { Question } from '../../entities/Question';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  constructor(public navCtrl: NavController, public toastCtrl: ToastController) {

  }

  ionViewDidLoad() {
    // Esta area se ejecuta al cargar la vista.
    // this.runDemo();
  }

  async runDemo() {
    const city1 = new City();
    city1.name = 'Cali';

    const cityRepository = getRepository('city') as Repository<City>;
    await cityRepository.save(city1);

    const client1 = new Client();
    client1.guid = this.guid();
    client1.code = 'code1';
    client1.customImage = 'test_test_test_test_test_test_test_test_test';
    client1.enabled = true;
    client1.name = 'nombre';
    client1.city = city1;

    const clientRepository = getRepository('client') as Repository<Client>;
    await clientRepository.save(client1);

    const paymentMethod1 = new PaymentMethod();
    paymentMethod1.name = 'Paypal';
    paymentMethod1.client = client1;

    const paymentMethodRepository = getRepository('payment_method') as Repository<PaymentMethod>;
    await paymentMethodRepository.save(paymentMethod1);

    // Prueba de guardar uno a muchos
    const photo1 = new Photo;
    photo1.url = "me.jpg";
    await getRepository(Photo).save(photo1);

    const photo2 = new Photo();
    photo2.url = "me-and-bears.jpg";
    await getRepository(Photo).save(photo2);

    const user = new User();
    user.name = "Pedro Perez";
    user.photos = [photo1];
    await getRepository(User).save(user);

    photo2.user = user;
    await getRepository(Photo).save(photo2);

    // Prueba de guardar muchos a muchos
    const category1 = new Category();
    category1.name = "animals";
    await getRepository(Category).save(category1);

    const category2 = new Category();
    category2.name = "zoo";
    await getRepository(Category).save(category2);

    const question = new Question();
    question.title = "¿Es un ejemplo?";
    question.text = "¿Para probar?";
    question.categories = [category1, category2];
    await getRepository(Question).save(question);
  }

  async getClientDemo() {
    const client1 = await getRepository(Client).createQueryBuilder().getOne();

    let toast = this.toastCtrl.create({
      message: 'El cliente obtenido es ' + client1.name,
      position: 'bottom',
      duration: 5000
    });
    toast.present();
  }

  /**
   * Generación de GUID donde sea necesario.
   */
  guid() {
    return this.s4() + this.s4() + '-' + this.s4() + '-' + this.s4() + '-' +
      this.s4() + '-' + this.s4() + this.s4() + this.s4();
  }

  s4() {
    return Math.floor((1 + Math.random()) * 0x10000)
      .toString(16)
      .substring(1);
  }

  /**
   * Area de eventos.
   */
  onInsertDB(event) {
    this.runDemo();
  }

  onViewFromDB(event) {
    this.getClientDemo();
  }
}



