import { Entity, PrimaryColumn, Column, OneToOne, JoinColumn } from 'typeorm';
import { City } from './City';

@Entity('client')
export class Client {

    @PrimaryColumn()
    guid: string;

    @Column()
    code: string;

    @Column()
    name: string;

    @Column()
    customImage: string;

    @Column()
    enabled: boolean;

    @OneToOne(type => City)
    @JoinColumn()
    city: City;
}


