import { Entity, Column, PrimaryGeneratedColumn, OneToOne, JoinColumn } from 'typeorm';
import { Client } from './Client';

@Entity("payment_method")
export class PaymentMethod {

    @PrimaryGeneratedColumn()
    id: number;

    @Column()
    name: string;

    @OneToOne(type => Client)
    @JoinColumn()
    client: Client;
}


